import { graphql, useStaticQuery } from 'gatsby';

const useArticulos = () => {

    const data = useStaticQuery(graphql`
    query {
        allDatoCmsArticulo {
            nodes {
                titulo
                id
                slug
                  descripcion
                categoria
                  tag
                imgdestacada {
                    fluid {
                        ...GatsbyDatoCmsFluid
                    }
                }
            }
        }
    }
    `);

    return data.allDatoCmsArticulo.nodes.map(articulo => ({
        titulo: articulo.titulo,
        id: articulo.id,
        descripcion: articulo.descripcion,
        categoria: articulo.categoria,
        tag: articulo.tag,
        imgdestacada: articulo.imgdestacada,
        slug: articulo.slug,
    }))
}
 
export default useArticulos;