import React from 'react';
import ParserHTML from '../components/particles/ParseHTML';
import { graphql, useStaticQuery } from 'gatsby';
import Image from 'gatsby-image';
import styled from '@emotion/styled';
import ArticuloPreview from '../components/articuloPreview';
import useArticulos from "../hooks/use-articulos";
import Meta from './meta';

const ImageBackground = styled(Image)`
    max-width: 500px;
    margin: 0 auto;
`

const ListadoArticulos = styled.ul`
  max-width:1200px;
  width:95%;
  margin:4rem auto 0 auto;

  @media (min-width:768px){
    display:grid;
    grid-template-columns: repeat(3, 1fr);
    column-gap: 3rem
  }
`


const ContenidoBlog = () => {

    const resultado = useStaticQuery(graphql`
    query{
          allDatoCmsPagina(filter: { slug: { eq: "blog" } }) {
                nodes {
                    titulo
                     contenido
                     seoMetaTags {
                        tags
                      }
                        imagen{
                              fluid {
                                ...GatsbyDatoCmsFluid      
                                }
                            }
               
                  }
            }
        }
     `);


    const { titulo, contenido, imagen, seoMetaTags } = resultado.allDatoCmsPagina.nodes[0];

    const articulos = useArticulos();

    // console.log(articulos);
    return (
        <>
            <Meta
                title={seoMetaTags.tags[0].content}
                description={seoMetaTags.tags[3].attributes.content}
                image={seoMetaTags.tags[13].attributes.content}
            />
            <div className="py-8 min-h-screen w-3/4 mx-auto">
                <div>

                    <div>
                        <h1 className="text-center font-black text-6xl mt-10 text-teal-900"

                        >{titulo}</h1>
                        <p className="text-justify text-4xl my-4 ">{ParserHTML(contenido)}</p>
                    </div>
                    <div>
                        <ImageBackground fluid={imagen.fluid} />
                    </div>


                </div>



                <div className="py-12">

                    <h2 className="text-center py-4 text-5xl">
                        Nuestros Articulos
                    </h2>

                    <ListadoArticulos className="text-center grid-cols-3 grid gap-4">
                        {articulos.map(articulo => (
                            <ArticuloPreview
                                key={articulo.id}
                                articulo={articulo}


                            />
                        ))}
                    </ListadoArticulos>

                </div>





            </div>
        </>
    );

}

export default ContenidoBlog;