import React from 'react';
import ParserHTML from '../components/particles/ParseHTML';
import Image from 'gatsby-image';
import {css} from '@emotion/react';
import {Link} from 'gatsby';

const ArticuloPreview = ({ articulo }) => {

    const {descripcion, imgdestacada, titulo, slug, tag, categoria,} = articulo;

    
    return (

        <li  className="sm:w-2/3 md:w-full mx-auto list-none">
            <Image fluid={imgdestacada.fluid}
            />

            <div className="bg-slate-50 p-8">
                <h3
                    css={css`
                        font-size:3rem;
                    `}
                
                    
                    >{titulo}</h3>
                    
                <p>{ParserHTML(descripcion)} </p>
  
                <Link className="py-6 px-5 block text-center bg-teal-600 m-10 transition-colors cursor-pointer uppercase rounded-lg hover:bg-teal-400 font-bold rounded text-white" to={slug}>Ver Tratamiento</Link>
            </div>
        </li>
    );

}

export default ArticuloPreview;